  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

.header {
  justify-content: space-between;
  margin-top: 23px;
  margin-bottom: 23px;
  display: flex;
}

.header__logo {
  margin-top: 10px;
  padding-left: 50px;
}

.header__logo-link-img {
  width: 40px;
  height: 40px;
}

.header__list {
  padding-right: 50px;
  list-style: none;
  display: flex;
}

.header__list-item {
  margin-left: 20px;
  position: relative;
}

.header__list-link {
  text-transform: uppercase;
  color: var(--text-color-main);
  text-decoration: none;
}

.header__list-link:hover, .is-active {
